@charset "UTF-8";
/*Обнуление*/
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@700&family=Mada:wght@700&family=Poppins:wght@400;700&family=Roboto:wght@700&display=swap");
@import url("https://fonts.googleapis.com/css?family=Source+Sans+Pro:600&display=swap");
* {
  padding: 0;
  margin: 0;
  border: 0;
}

*, *:before, *:after {
  box-sizing: border-box;
}

:focus, :active {
  outline: none;
}

a:focus, a:active {
  outline: none;
}

nav, footer, header, aside {
  display: block;
}

html, body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input, button, textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a, a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  font-weight: inherit;
}

/*--------------------*/
@font-face {
  font-family: "Vasek";
  src: url("../fonts/Vasek Italic_0.eot");
  src: local("☺"), url("../fonts/Vasek Italic_0.woff") format("woff"), url("../fonts/Vasek Italic_0.ttf") format("truetype"), url("../fonts/Vasek Italic_0.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}
/** Generated by FG **/
@font-face {
  font-family: "Wander";
  src: url("../fonts/Wander Over Yonder Regular.eot");
  src: local("☺"), url("../fonts/Wander Over Yonder Regular.woff") format("woff"), url("../fonts/Wander Over Yonder Regular.ttf") format("truetype"), url("../fonts/Wander Over Yonder Regular.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}
.wrapper {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.fullscreen {
  min-height: 100vh;
  background: url(../img/fon3.jpg) center/cover no-repeat;
  display: flex;
  padding: 20px 0;
}
.fullscreen__body {
  flex: 1 1 auto;
  width: 100%;
  max-width: 1000px;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.fullscreen__title {
  font-family: "Wander";
  font-size: 60px;
  color: #3b68e6;
}
.fullscreen__text {
  font-family: "Vasek";
  font-weight: 900;
  margin: 20px 0px 0px 0px;
  font-size: 40px;
  color: #3b68e6;
}
.fullscreen__text:hover:first-letter {
  color: red;
}

body._pc .menu__list > li:hover .menu__sub-list {
  opacity: 1;
  visibility: visible;
  transform: translate(0px 0px);
  pointer-events: all;
}

body._touch .menu__list > li {
  display: flex;
  align-items: center;
}
body._touch .menu__list > li._active .menu__sub-list {
  opacity: 1;
  visibility: visible;
  transform: translate(0px 0px);
  pointer-events: all;
}
@media (max-width: 767px) {
  body._touch .menu__list > li._active .menu__sub-list {
    display: block;
  }
}
body._touch .menu__list > li._active .menu__arrow {
  transform: rotate(-180deg);
}
body._touch .menu__link {
  flex: 1 1 auto;
}
body._touch .menu__arrow {
  display: block;
  width: 0;
  height: 0;
  margin: 0px 0px 0px 5px;
  transition: transform 0.3s ease 0s;
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
  border-top: 10px solid #3b68e6;
}

body._lock {
  overflow: hidden;
}

.header {
  background-color: #e9e6db;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
}
.header__container {
  max-width: 100%;
  margin: 0px auto;
  display: flex;
  padding: 0px 30px;
  align-items: center;
  justify-content: space-between;
  min-height: 70px;
}
.hedar__logo {
  background: url(../img/logo/logo.jpg) center/cover no-repeat;
  border-radius: 50%;
  background-color: #000;
  position: relative;
  z-index: 5;
  width: 40px;
  height: 40px;
}

.menu__icon {
  display: none;
}
@media (max-width: 767px) {
  .menu__icon._active span {
    transform: scale(0) translate(0px, -50%);
  }
}
@media (max-width: 767px) {
  .menu__icon._active::before {
    top: 50%;
    transform: rotate(-45deg) translate(0px, -50%);
  }
}
@media (max-width: 767px) {
  .menu__icon._active::after {
    bottom: 50%;
    transform: rotate(45deg) translate(0px, 50%);
  }
}
@media (max-width: 767px) {
  .menu__icon {
    z-index: 5;
    display: block;
    position: relative;
    width: 30px;
    height: 18px;
    cursor: pointer;
  }
}
@media (max-width: 767px) {
  .menu__icon span, .menu__icon::after, .menu__icon::before {
    left: 0;
    position: absolute;
    height: 10%;
    width: 100%;
    transition: all 0.3s ease 0s;
    background-color: #000;
  }
}
@media (max-width: 767px) {
  .menu__icon::before {
    top: 0;
  }
}
@media (max-width: 767px) {
  .menu__icon::before, .menu__icon::after {
    content: "";
  }
}
@media (max-width: 767px) {
  .menu__icon::after {
    bottom: 0;
  }
}
@media (max-width: 767px) {
  .menu__icon span {
    top: 50%;
    transform: scale(1) translate(0px, -50%);
  }
}
.menu__body._active {
  left: 0;
}
@media (max-width: 767px) {
  .menu__body {
    position: fixed;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background-color: rgba(172, 160, 51, 0.699);
    padding: 100px 30px 30px 30px;
    transition: left 0.3s ease 0s;
    overflow: auto;
  }
}
@media (max-width: 767px) {
  .menu__body::before {
    content: "";
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    height: 70px;
    background-color: #e9e6db;
    z-index: 2;
  }
}
@media (min-width: 767px) {
  .menu__list {
    display: flex;
    align-items: center;
  }
}
.menu__list > li {
  position: relative;
  margin: 0px 0px 0px 20px;
}
@media (min-width: 767px) {
  .menu__list > li {
    padding: 10px 0;
  }
}
@media (max-width: 767px) {
  .menu__list > li {
    flex-wrap: wrap;
    margin: 0px 0px 30px 0px;
  }
}
@media (max-width: 767px) {
  .menu__list > li:last-child {
    margin-bottom: 0;
  }
}
.menu__link {
  color: #3b68e6;
  font-size: 18px;
}
@media (max-width: 767px) {
  .menu__link {
    font-size: 24px;
  }
}
.menu__link:hover {
  text-decoration: underline;
}
.menu__sub-list {
  position: absolute;
  top: 70%;
  right: 0;
  background-color: #3b68e6;
  padding: 15px;
  min-width: 200px;
}
.menu__sub-list li {
  margin: 0px 0px 10px 0px;
}
.menu__sub-list li:last-child {
  margin: 0;
}
@media (min-width: 767px) {
  .menu__sub-list {
    transform: translate(0px, 10%);
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    transition: all 0.3s ease 0s;
  }
}
@media (max-width: 767px) {
  .menu__sub-list {
    position: relative;
    background-color: #fff;
    flex: 1 1 100%;
    margin: 20px 0px 0px 0px;
    display: none;
  }
}
.menu__sub-link {
  color: #e9e6db;
}
.menu__sub-link:hover {
  text-decoration: underline;
}
@media (max-width: 767px) {
  .menu__sub-link {
    font-size: 20px;
    color: #000;
  }
}
.menu_arrow {
  display: none;
}

.about__section {
  display: flex;
}
.about__container {
  max-width: 1200px;
  margin: 0 auto;
}
.about__title {
  height: 80px;
  text-align: center;
  line-height: 80px;
  border-top: 2px solid rgb(211, 26, 112);
  border-bottom: 2px solid #000;
}
.about__title h1 {
  font-family: "Wander";
  font-size: 60px;
  color: #3b68e6;
}
.about__info {
  display: flex;
}
.about__img {
  margin: 30px;
}
.about__img img {
  width: 300px;
  height: 300px;
  border-radius: 50%;
}
.about__data {
  display: flex;
  margin: 10px 0px 0px 0px;
  font-size: 20px;
}

.abount__name {
  font-family: "Wander";
  font-size: 30px;
  color: #3b68e6;
  margin: 0px 8px 0px 0px;
  font-weight: 700;
}
.abount__value {
  text-align: center;
  font-size: 30px;
  font-family: "Vasek";
  font-weight: 500;
  color: #3b68e6;
}

.page {
  padding: 70px 0px 30px 0px;
}
.page__section {
  padding: 30px;
  max-width: 1200px;
  margin: 0px auto;
}
.page__section-2 {
  background: #e9e6db;
}
.page__section-3 {
  background: #af6f92;
}
.page__title {
  font-size: 40px;
  margin: 0px 0px 20px 0px;
}
.page__text {
  font-size: 16px;
  line-height: 150%;
}
.page__text p {
  margin: 0px 0px 20px 0px;
}
.page__sub-title {
  font-size: 30px;
  margin: 0px 0px 20px 0px;
}